import React, { useCallback, useState } from "react";
import { _t } from "../../../utils/Translator";
import SearchTagSelect from "../searchselect/SearchTagSelect";
import type { SearchTagItem } from "../searchselect/SearchTagSelect";
import { TagItem } from "../tags/tag";

export interface PartyItem {
  id: number;
  caseId: number;
  name: string;
  reference: string;
  salutation: string;
  email: string;
  tags: TagItem[];
}

interface PartyProps {
  parties: PartyItem[];
  selectedParties: PartyItem[];
  onSelectedPartiesChange: (items: PartyItem[]) => void;
}

const normalize = (s: string) => (s || "").trim().replace(/\s+/g, " ");
const makeKey = (p: PartyItem) =>
  `${normalize(p.name).toLowerCase()}||${normalize(p.reference).toLowerCase()}`;

const Party: React.FC<PartyProps> = ({
  parties,
  selectedParties,
  onSelectedPartiesChange,
}) => {
  const [pickerValue, setPickerValue] = useState<PartyItem | null>(null);

  // Handle selection directly: add immediately
  const handlePickerSelected = (item: SearchTagItem | PartyItem | null) => {
    const party = (item as PartyItem) || null;
    setPickerValue(party);
    if (!party) return;

    const key = makeKey(party);
    const exists = selectedParties.some((sp) => makeKey(sp) === key);
    if (!exists) {
      onSelectedPartiesChange([...selectedParties, party]);
    }
    setPickerValue(null); // reset picker after add
  };

  const removeSelected = useCallback(
    (party: PartyItem) => {
      const key = makeKey(party);
      onSelectedPartiesChange(
        selectedParties.filter((sp) => makeKey(sp) !== key)
      );
    },
    [selectedParties, onSelectedPartiesChange]
  );

  return (
    <div>
      <div className="page-section-title">{_t("Parties")}</div>

      <SearchTagSelect
        items={parties as unknown as SearchTagItem[]}
        selectedItem={pickerValue as unknown as SearchTagItem}
        onItemSelected={handlePickerSelected}
        selectText={_t("Select party")}
        searchText={_t("Search party")}
      />

      {/* Selected pills */}
      <div className="mt-3">
        {selectedParties.map((p) => (
          <span
            key={`${p.id}-${p.reference || ""}`}
            className="badge rounded-pill bg-primary me-1 mb-2"
            title={p.reference ? `${p.name} (${p.reference})` : p.name}
          >
            {p.name}
            {p.reference ? ` (${p.reference})` : ""}
            <i
              className="fa fa-times ms-2"
              style={{ cursor: "pointer" }}
              onClick={() => removeSelected(p)}
              aria-hidden="true"
            />
          </span>
        ))}
      </div>
    </div>
  );
};

export default Party;
