import { _t } from "../../../utils/Translator";
import Case, { CaseItem } from "../case/Case";
import Document, { DocumentItem } from "../document/Document";
import { ComposeMainPages } from "../composemain/ComposeMain";
import React, { useEffect, useState } from "react";
import api from "../../api";
import { ContentType, HttpVerb, sendHttpRequest } from "../../../utils/httpRequest";
import Error from "../error/Error";

interface AddDocumentsPageProps {
  selectedCase: CaseItem;
  onCaseSelected: (m: CaseItem) => void;
  goToPage: (page: ComposeMainPages) => void;
  getConnectionToken: () => string;
}

const AddDocumentsPage: React.FC<AddDocumentsPageProps> = ({
  selectedCase,
  onCaseSelected,
  goToPage,
  getConnectionToken,
}) => {
  const [documents, setDocuments] = useState<DocumentItem[]>([]);
  const [selectedDocument, setSelectedDocument] = useState<DocumentItem | null>(null);
  const [error, setError] = useState<string>("");

  useEffect(() => {
    setError("");
    setSelectedDocument(null);
    setDocuments([]);

    const cid = selectedCase?.id;
    if (!cid) return;

    const token = getConnectionToken();

    const documentsPromise = sendHttpRequest(
      HttpVerb.POST,
      api.baseURL + api.getCaseDocuments, // /outlook/get_case_documents
      ContentType.Json,
      token,
      { case_id: cid },
      true
    ).promise;

    documentsPromise
      .then((dResStr) => {
        const dRes = JSON.parse(dResStr);
        if (!dRes.result?.success) {
          setError(String(dRes.result?.error || "Failed to load documents"));
          setDocuments([]);
        } else {
          setDocuments(dRes.result.data?.documents || []);
        }
      })
      .catch((e) => {
        setError(String(e));
        setDocuments([]);
      });
  }, [selectedCase?.id, getConnectionToken]);

  const getCancelButton = () => (
    <button
      title="Return"
      type="button"
      className="btn btn-default"
      onClick={() => goToPage(ComposeMainPages.ComposeMainPage)}
    >
      <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
    </button>
  );

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-paperclip fa-fw text-primary"></i>&nbsp; {_t("Add Attachments")}
      </div>

      <Case selectedCase={selectedCase} onCaseSelected={onCaseSelected} />

      <Error error={error} setError={setError} />

      {selectedCase && (
        <Document
          documents={documents}
          selectedDocument={selectedDocument}
          onDocumentSelected={setSelectedDocument}
          getConnectionToken={getConnectionToken}
        />
      )}

      <div className="page-button-container">
        <span>{getCancelButton()}</span>
      </div>
    </div>
  );
};

export default AddDocumentsPage;
