import React, { useState, useEffect } from "react";
import Login from "./login/Login";
import ComposeMain from "./composemain/ComposeMain";
import ReadMain from "./readmain/ReadMain";
import { saveTranslations } from "../../utils/Translator";
import { ContentType, HttpVerb, sendHttpRequest } from "../../utils/httpRequest";
import api from "../api";

/* global localStorage */

enum Page {
  Login,
  Main,
}

export enum Mode {
  Read,
  Compose,
}

interface AppProps {
  isOfficeInitialized: boolean;
  itemChangedRegister: (callback: () => void) => void;
  mode: Mode;
}

const App: React.FC<AppProps> = ({ isOfficeInitialized, itemChangedRegister, mode }) => {
  const [mainKey, setMainKey] = useState(0);
  const [page, setPage] = useState<Page>(Page.Login);

  /**
   * Loads translations from Odoo after successful login.
   */
  const getTranslations = async () => {
    try {
      const token = localStorage.getItem("odooConnectionToken");
      const request = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getTranslations,
        ContentType.Json,
        token,
        true
      );
      const response = JSON.parse(await request.promise);
      saveTranslations(response.result);
    } catch (error) {
      console.log("An error occurred when getting translations from Odoo: " + String(error));
    }
  };

  /**
   * Called when user successfully logs in.
   * Stores the token and switches to the main page.
   */
  const connect = (token: string) => {
    localStorage.setItem("odooConnectionToken", token);
    getTranslations();
    setPage(Page.Main);
  };

  /**
   * Called when the user logs out.
   * Clears all stored session data.
   */
  const disconnect = () => {
    localStorage.removeItem("odooConnectionToken");
    localStorage.removeItem("baseURL");
    localStorage.removeItem("translations");
    localStorage.removeItem("translationsTimestamp");
    setPage(Page.Login);
  };

  /**
   * Registers callback for item changes (used to force rerender),
   * and tries to restore previous session if token and baseURL exist.
   */
  useEffect(() => {
    const onItemChanged = () => setMainKey(prevKey => prevKey + 1);
    itemChangedRegister(onItemChanged);

    // Auto-login if a valid token and baseURL are stored
    const token = localStorage.getItem("odooConnectionToken");
    const baseURL = localStorage.getItem("baseURL");

    if (token && baseURL) {
      api.baseURL = baseURL;
      setPage(Page.Main);
      getTranslations();
    } else {
      setPage(Page.Login);
    }
  }, [itemChangedRegister]);

  /**
   * Selects the correct page to render: login or main (read/compose).
   */
  const getPage = () => {
    const loginPage = <Login onLogin={connect} />;
    const mainPage =
      mode === Mode.Read ? (
        <ReadMain key={mainKey} disconnect={disconnect} />
      ) : (
        <ComposeMain key={mainKey} disconnect={disconnect} />
      );

    return page === Page.Login ? loginPage : mainPage;
  };

  if (!isOfficeInitialized) {
    // Show a loading screen while Office initializes
    return (
      <div className="bg-light">
        <div className="login d-flex align-items-center justify-content-center vh-100">
          <div className="w-100 text-center">
            <i className="fa fa-spinner fa-spin fa-5x text-primary"></i>
            <h1 className="mb-5">Getting ready...</h1>
          </div>
        </div>
      </div>
    );
  }

  return <div>{getPage()}</div>;
};

export default App;
