import * as React from "react";
import { useState, useEffect } from "react";
import Case, { CaseItem } from "../case/Case";
import Party, { PartyItem } from "../party/Party";
import { sendHttpRequest, HttpVerb, ContentType } from "../../../utils/httpRequest";
import api from "../../api";
import { _t } from "../../../utils/Translator";
import { ComposeMainPages } from "../composemain/ComposeMain";
import MailTemplate, { MailTemplateItem } from "../mail_template/MailTemplate";
import Error from "../error/Error";
import Progress from "../progress/Progress";

interface GenerateMailPageProps {
  mailTemplates: MailTemplateItem[];
  selectedMailTemplate: MailTemplateItem | null;
  onMailTemplateSelected: (t: MailTemplateItem | null) => void;
  selectedCase: CaseItem | null;
  onCaseSelected: (c: CaseItem | null) => void;
  getConnectionToken: () => string;
  goToPage: (page: ComposeMainPages) => void;
}

const GenerateMailPage: React.FC<GenerateMailPageProps> = ({
  mailTemplates,
  selectedMailTemplate,
  onMailTemplateSelected,
  selectedCase,
  onCaseSelected,
  getConnectionToken,
  goToPage,
}) => {
  const [parties, setParties] = useState<PartyItem[]>([]);
  const [selectedParties, setSelectedParties] = useState<PartyItem[]>([]);
  const [error, setError] = useState<string>("");
  const [processing, setProcessing] = useState<boolean>(false);

  // Load parties for the selected case
  useEffect(() => {
    setError("");
    setSelectedParties([]);
    setParties([]);

    const caseId = selectedCase?.id;
    if (!caseId) return;

    const req = sendHttpRequest(
      HttpVerb.POST,
      api.baseURL + api.getCaseParties, // maps to /outlook/get_case_parties
      ContentType.Json,
      getConnectionToken(),
      { case_id: caseId },
      true
    );

    req.promise
      .then((resStr) => {
        const res = JSON.parse(resStr);
        if (!res.result?.success) {
          setError(String(res.result?.error || "Failed to load parties"));
          return;
        }
        setParties(res.result.data?.parties || []);
      })
      .catch((e) => setError(String(e)));
  }, [selectedCase?.id, getConnectionToken]);

  const generateMail = async () => {
    if (!selectedMailTemplate || !selectedCase) return;
    setProcessing(true);
    setError("");

    const selected_party_ids = selectedParties.map((p) => p.id);

    try {
      const req = sendHttpRequest(
        HttpVerb.POST,
        api.baseURL + api.getMailData,
        ContentType.Json,
        getConnectionToken(),
        {
          mail_template_id: selectedMailTemplate.id,
          case_id: selectedCase.id,
          selected_party_ids,
        },
        true
      );

      const res = JSON.parse(await req.promise);
      if (!res.result?.success) {
        setError(String(res.result?.error || "Failed to generate mail"));
        setProcessing(false);
        return;
      }

      const data = res.result.data?.mail_data || {};
      const subject: string | null = data.subject || null;
      const bodyHtml: string | null = data.body_html || null;
      const emailTo: string[] = Array.isArray(data.email_to) ? data.email_to : [];
      const emailCc: string[] = Array.isArray(data.email_cc) ? data.email_cc : [];

      // Apply to Outlook compose item
      if (subject) Office.context.mailbox.item.subject.setAsync(subject, () => {});
      if (bodyHtml)
        Office.context.mailbox.item.body.setAsync(bodyHtml, { coercionType: Office.CoercionType.Html }, () => {});
      if (emailTo.length) Office.context.mailbox.item.to.setAsync(emailTo, () => {});
      if (emailCc.length) Office.context.mailbox.item.cc.setAsync(emailCc, () => {});
    } catch (e) {
      setError(String(e));
    } finally {
      setProcessing(false);
    }
  };

  const getCancelButton = () => (
    <button
      title="Return"
      type="button"
      className="btn btn-default"
      onClick={() => goToPage(ComposeMainPages.ComposeMainPage)}
    >
      <i className="fa fa-arrow-left text-primary" aria-hidden="true"></i>
    </button>
  );

  const getGenerateMailButton = () => (
    <button title="Confirm" type="button" className="btn btn-primary" onClick={generateMail}>
      {_t("Confirm")}
    </button>
  );

  return (
    <div>
      <div className="page-title">
        <i className="fa fa-pencil-square-o fa-fw text-primary"></i>&nbsp;{_t("Generate Email")}
      </div>

      <Error error={error} setError={setError} />

      <Case selectedCase={selectedCase} onCaseSelected={onCaseSelected} />

      {selectedCase && (
        <MailTemplate
          mailTemplates={mailTemplates}
          selectedMailTemplate={selectedMailTemplate}
          onMailTemplateSelected={onMailTemplateSelected}
        />
      )}

      {selectedMailTemplate && selectedCase && (
        <Party
          parties={parties}
          selectedParties={selectedParties}
          onSelectedPartiesChange={setSelectedParties}
        />
      )}

      <div className="d-flex justify-content-between page-button-container">
        <span>{getCancelButton()}</span>
        {selectedMailTemplate && selectedCase && (
          <span>{getGenerateMailButton()}</span>
        )}
        </div>

      <Progress processing={processing} />
    </div>
  );
};

export default GenerateMailPage;
